* {
	margin: 0;
	padding: 0;
}

html, body {
	box-sizing: border-box;
	width: 100vw;
}

.container {
	display: flex;
	flex-direction: row;
	width: 100vw;
	height: 100vh;
}

.row {
	display: flex;
	flex-direction: row;
}

.column {
	display: flex;
	flex-direction: column;
}

.inbox {
	height: 100%;
	background-color: aqua;
	max-width: 70px;
	min-width: 70px;
}

.letters {
	height: 100%;
	background-color: blue;
}

.writter {
	height: 100%;
	background-color: darkviolet;
}



